<template>
  <el-drawer
    :model-value="modelValue"
    direction="ltr"
    size="80%"
    :show-close="false"
    @update:model-value="emit('update:modelValue', $event)"
  >
    <div class="container">
      <div class="logo-wrapper">
        <el-image src="/assets/color-logo.png" class="logo" />
      </div>
      <div class="inner">
        <div class="nav">
          <el-button
            v-for="item in navs"
            :key="item.name"
            :type="current(item.path) ? undefined : `text`"
            round
            :class="{
              normal: !current(item.path),
              current: current(item.path),
            }"
            :color="current(item.path) ? secondaryBackground : undefined"
            @click="navTo(item.path)"
          >
            <div class="btn-inner">
              <span class="material-icons-outlined">{{ item.icon }}</span>
              <span>{{ item.name }}</span>
            </div>
          </el-button>
        </div>
        <div class="end">
          <SwitchNetworkDialog v-model="switchNetwork"></SwitchNetworkDialog>
          <SwitchLanguage v-model="switchLanguage"></SwitchLanguage>
          <transition name="fade">
            <el-button
              v-if="chainName"
              :color="secondaryBackground"
              round
              class="current bold"
              @click="switchNetwork = true"
              >{{ chainName }}</el-button
            >
          </transition>

          <div class="row">
            <el-button type="text" @click="switchLanguage = true">
              <div class="btn-inner">
                <span class="material-icons-outlined">language</span>
                <span>{{ t("language") }}</span>
              </div>
            </el-button>
            <el-button
              v-if="!account"
              type="text"
              @click="web3Operator.connect"
            >
              <div class="btn-inner">
                <span class="material-icons-outlined">account_balance_wallet</span>
                <span>{{ t("link") }}</span>
              </div>
            </el-button>
            <el-button
              v-else
              :color="secondaryBackground"
              round
              class="current"
              @click="web3Operator.disconnect"
            >
              {{
                `${account.slice(0, 5)}...${account.slice(account.length - 4)}`
              }}
            </el-button>
          </div>
        </div>
      </div>
    </div>
  </el-drawer>
</template>

<script lang="ts" setup>
import { useRoute } from "vue-router";
import { useNavTo } from "../../router";
import { INavItem } from "../types";
import { web3Operator, web3InfoGetter } from "../../store";
import SwitchNetworkDialog from "./SwitchNetworkDialog.vue";
import SwitchLanguage from "./SwitchLanguage.vue";
import { ref } from "vue";
import { secondaryBackground } from "../../styles/color";
import { useI18n } from "vue-i18n";

const { t } = useI18n({
  messages: { en: { link: "Connect wallet" }, "zh-CN": { link: "连接钱包" } },
});

const account = web3InfoGetter.account;
const chainName = web3InfoGetter.chain.name;

defineProps<{ modelValue: boolean; navs: INavItem[] }>();
const emit = defineEmits<{ (e: "update:modelValue", value: boolean): void }>();
const route = useRoute();
function current(path: string) {
  return route.fullPath === path;
}

const navTo = useNavTo(() => emit("update:modelValue", false));

const switchNetwork = ref(false);
const switchLanguage = ref(false);
</script>

<style lang="scss" scoped>
.container,
.nav,
.end,
.container > .inner {
  display: flex;
  flex-direction: column;
}

.container {
  height: 100%;
  gap: 128px;
  --el-border-radius-round: 10px;
  & > .inner {
    justify-content: space-between;
    flex: 1;
  }
}

.logo-wrapper {
  display: flex;
  justify-content: center;
  transform: translate3d(0, 0, 0);
}

.logo {
  :deep(img) {
    image-rendering: -webkit-optimize-contrast;
    height: 50px;
  }
}

.nav {
  align-items: stretch;
  :deep(.el-button + .el-button) {
    margin-left: unset;
  }
}

.normal {
  padding-left: 20px;
}

.current {
  color: black;
  .btn-inner {
    color: black;
  }
}

.end {
  gap: 16px;
}

.btn-inner,
.row {
  display: flex;
  align-items: center;
}

.btn-inner {
  gap: 16px;
  font-weight: bold;
  color: var(--el-text-color-regular);
}

.row {
  justify-content: space-between;
  .btn-inner {
    gap: 12px;
  }
}

.bold {
  font-weight: bold;
}
</style>
